.inputBox{
    margin: 3% auto;
    padding: 3%;
    height: 3rem;
    width: 30rem;
    background-color: rgb(55, 255, 255);
    text-align: center;
    font-family: sans-serif;
    border-radius: 10px;
}

#enteredTask{
    right: 4rem;
    width: 15rem;
    position: relative;
    top: 0.6rem;
    border-radius: 5px;
    height: 2rem;
    text-align: center;
    font-weight: 600;
}

#addButtom{
    height: 2.3rem;
    border-radius: 5px;
    text-align: center;
    width: 6rem;
    font-weight: 600;
    position: relative;
    top: 0.6rem;
    left: 4rem;
}

